import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

// знаходимо контейнер галереї
const galleryContainer = document.querySelector('.gallery');

// створюємо розмітку елемента галереї
const createGalleryItem = ({ preview, original, description }) => {
  return `
        <li class="gallery-item">
            <a class="gallery-link" href="${original}">
                <img
                    class="gallery-image"
                    src="${preview}"
                    alt="${description}"
                />
            </a>
        </li>
    `;
};

// створюємо розмітку всієї галереї
const galleryMarkup = images.map(createGalleryItem).join('');

// додаємо розмітку всієї галереї в контейнер
galleryContainer.innerHTML = galleryMarkup;

const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionPosition: 'bottom',
  captionDelay: 250,
});

function setColor(selector, color) {
  const elements = document.querySelectorAll(selector);
  elements.forEach(element => {
    element.style.color = color;
  });
}

lightbox.on('shown.simplelightbox', function () {
  document.querySelector('.sl-wrapper').style.backgroundColor =
    'rgba(46, 47, 66, 0.80)';
  setColor('.sl-wrapper .sl-close, .sl-counter, .sl-navigation button', '#fff');
});
